.footer{
   position: relative;
   background: var(--color-text);
   color: #fff;
   &__container{
      background: var(--color-text);
      padding: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
     & span{
        display: inline-block;
        margin-right: 15px;
     }
   }
   &__social{
      & a{
         display: inline-block;
         margin-right: 10px;
         &:hover{
            color: var(--color-green);
         }
      }
   }
   &__copy{
      width: 100%;
      text-align: center;
      font-size: 15px;
      opacity: 0.7;
   }
}